import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';

import landingPageImage from '../images/landingPageImage.jpg';

const styles = theme => ({
    heroContainer: {
        backgroundImage: 'url(' + landingPageImage + ')',
        backgroundSize: 'cover',
        display: 'flex',
        flexDirection: 'column',
        textAlign: 'center',
        height: 'calc(100vh + 20px)',
        filter: 'grayscale(10%) sepia(47%)',
        position: 'relative'
    },
    headline: {
        flex: 1,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-end',
        fontFamily: "'Fugaz One', cursive",
        fontSize: '4.5vh',
        textShadow: '2px 2px #5c4f3c',
        color: 'white',
        zIndex: 2,
    },
    headlineText: {
        marginBottom: '0.1em',
        letterSpacing: '8px',
    },
    subHeadline: {
        marginTop: '0.1em',
        flex: 1,
        fontSize: '24px',
        lineHeight: '30px',
        color: 'white',
        zIndex: 2,
        letterSpacing: '1px',
    },
    subHeadlineBold: {
        fontWeight: 'bold',
        textTransform: 'uppercase',
        textShadow: '1px 1px #5c4f3c',
        fontFamily: "'Fugaz One', cursive",
        letterSpacing: '3px',
    },
    andText: {
        margin: '0 0.5%'
    },
    imageFade:{
        position:"absolute",
        width:"100%",
        height: "100%",
        backgroundColor:"black",
        opacity: 0.4,
        zIndex: 1,
    },
});

class Hero extends Component {
    constructor(props) {
        super(props);
        this.classes = props.classes;
    }

    render() {
        return (
            <div className={this.classes.heroContainer}>
                <div className={this.classes.imageFade}/>
                <div className={this.classes.headline}>
                    <h1 className={this.classes.headlineText}>Hi, I'm Steven.</h1>
                </div>
                <div className={this.classes.subHeadline}>
                    <p>I'm a <span className={this.classes.subHeadlineBold}>software engineer</span> and <span className={this.classes.subHeadlineBold}>web developer</span></p>
                </div>
            </div>
        );
    }
}

Hero.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Hero);



