import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Avatar from 'material-ui/Avatar';

import ProfileImage from '../images/profile.jpg';
import EmailImage from '../images/email.svg';
import LinkedInImage from '../images/linkedin.svg';
import GithubImage from '../images/github.svg';

import Grid from 'material-ui/Grid';

const styles = theme => ({
    contactContainer: {
        padding: '5%',
        backgroundColor: '#fff4d3',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
    },
    title: {
        margin: '0',
        color: '#37556a',
        letterSpacing: '1px',
        textTransform: 'uppercase',
        fontFamily: "'Fugaz One', cursive",
        fontSize: '42px',
    },
    avatar: {
        margin: '5%',
        height: '200px',
        width: '200px',
    },
    image: {
        height: '80px',
        width: '80px',
    },
    links: {
        padding: '0 20%',
    },
    link: {
        display: 'flex',
        justifyContent: 'center',
    },
});

class Contact extends Component {
    constructor(props) {
        super(props);
        this.classes = props.classes;
    }

    render() {
        return (
            <div className={this.classes.contactContainer}>
                <h1 className={this.classes.title}>Get in Touch!</h1>
                <Avatar alt="Steven Jing" src={ProfileImage} className={this.classes.avatar}/>
                <Grid container spacing={24} className={this.classes.links}>
                    <Grid item xs={12} sm={4}>
                        <div className={this.classes.link}>
                            <a href="mailto:steven.jing@gmail.com">
                                <img className={this.classes.image} src={EmailImage} alt="Email"/>
                            </a>
                        </div>
                    </Grid>
                    <Grid item xs={12} sm={4}>
                        <div className={this.classes.link}>
                            <a href="https://www.linkedin.com/in/stevenjing" target="_blank" rel="noopener noreferrer">
                                <img className={this.classes.image} src={LinkedInImage} alt="LinkedIn"/>
                            </a>
                        </div>
                    </Grid>
                    <Grid item xs={12} sm={4}>
                        <div className={this.classes.link}>
                            <a href="https://www.github.com/stevenjing" target="_blank" rel="noopener noreferrer">
                                <img className={this.classes.image} src={GithubImage} alt="Github"/>
                            </a>
                        </div>
                    </Grid>
                </Grid>
            </div>
        );
    }
}

Contact.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Contact);



