import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';

import CalendarImage from '../images/calendar.svg';
import NimbusImage from '../images/nimbus.svg';
import PersonalysisImage from '../images/personalysis.svg';
import TetrisImage from '../images/tetris.svg';

import Grid from 'material-ui/Grid';

import Scroll from 'react-scroll';
const ScrollElement = Scroll.Element;

const styles = theme => ({
    portfolioContainer: {
        backgroundColor: '#37556a',
        padding: '5%',
        color: '#fff4d3',
        textAlign: 'center',
    },
    portfolioHeader: {
        paddingBottom: '2%',
    },
    portfolioTitle: {
        margin: '0',
        textTransform: 'uppercase',
        fontFamily: "'Fugaz One', cursive",
        fontSize: '42px',
        letterSpacing: '8px',
    },
    image: {
        height: '150px',
        width: '150px',
    },
    project: {
        padding: '20px',
    },
    projectTitle: {
        letterSpacing: '1px',
    },
    projectDescription: {
        fontStyle: 'italic',
    },
});

class Hero extends Component {
    constructor(props) {
        super(props);
        this.classes = props.classes;
    }

    render() {
        return (
            <div>
                <ScrollElement name="portfolio" />
                <div className={this.classes.portfolioContainer}>
                    <div className={this.classes.portfolioHeader}>
                        <h1 className={this.classes.portfolioTitle}>Personal Projects</h1>
                        <p>I love bringing new ideas to life during my free time!</p>
                    </div>
                    <Grid container spacing={24}>
                        <Grid item xs={12} sm={6}>
                            <div className={this.classes.project}>
                                <a href="https://github.com/stevenjing/nimbus-mobile" target="_blank" rel="noopener noreferrer">
                                    <img className={this.classes.image} src={NimbusImage} alt="Nimbus"/>
                                </a>
                                <h2 className={this.classes.projectTitle} >Nimbus</h2>
                                <p className={this.classes.projectDescription} >A geolocation based social network for sharing photos</p>
                            </div>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <div className={this.classes.project}>
                                <a href="https://github.com/stevenjing/personalysis-ui" target="_blank" rel="noopener noreferrer">
                                    <img className={this.classes.image} src={PersonalysisImage} alt="Personalysis"/>
                                </a>
                                <h2 className={this.classes.projectTitle} >Personalysis</h2>
                                <p className={this.classes.projectDescription} >A genomics-based personality assessment application</p>
                            </div>
                        </Grid>
                    </Grid>
                    <Grid container spacing={24}>
                        <Grid item xs={12} sm={6}>
                            <div className={this.classes.project}>
                                <a href="https://github.com/stevenjing/friends" target="_blank" rel="noopener noreferrer">
                                    <img className={this.classes.image} src={CalendarImage} alt="Friends"/>
                                </a>
                                <h2 className={this.classes.projectTitle} >Friends</h2>
                                <p className={this.classes.projectDescription} >An event planner for social groups</p>
                            </div>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <div className={this.classes.project}>
                                <a href="https://github.com/stevenjing/tetrjs" target="_blank" rel="noopener noreferrer">
                                    <img className={this.classes.image} src={TetrisImage} alt="Tetr.js"/>
                                </a>
                                <h2 className={this.classes.projectTitle} >Tetr.js</h2>
                                <p className={this.classes.projectDescription} >A tetris game simulator made with AngularJS</p>
                            </div>
                        </Grid>
                    </Grid>
                </div>
            </div>
        );
    }
}

Hero.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Hero);



