import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import classNames from 'classnames';

import FrontEndImage from '../images/frontend.svg';
import BackEndImage from '../images/backend.svg';
import ReactImage from '../images/react.svg';
import AngularImage from '../images/angular.svg';
import NodeImage from '../images/node.svg';

import Grid from 'material-ui/Grid';

import Scroll from 'react-scroll';
const ScrollElement = Scroll.Element;

const styles = theme => ({
    aboutContainer: {
        backgroundColor: '#fff4d3',
        padding: '5%',
        color: '#37556a',
    },
    header: {
        textAlign: 'center',
        paddingBottom: '2%',
    },
    title: {
        margin: '0',
        fontFamily: "'Fugaz One', cursive",
        fontSize: '42px',
        textTransform: 'uppercase',
        letterSpacing: '8px',
    },
    image: {
        height: '150px',
        width: '150px',
    },
    itemContainer: {
        flex: 1,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        textAlign: 'center',
    },
    frontendContainer: {
        // paddingLeft: '13%',
        // paddingRight: '2%',
    },
    backendContainer: {
        // paddingLeft: '2%',
        // paddingRight: '13%',
    },
    typeDescription: {
        flex: 1,
    },
    typeDescriptionText: {
        lineHeight: '1.8',
    },
    toolsContainer: {
        display: 'flex',
    },
});

class About extends Component {
    constructor(props) {
        super(props);
        this.classes = props.classes;
    }

    render() {
        return (
            <div>
                <ScrollElement name="about" />
                <div className={this.classes.aboutContainer}>
                    <div className={this.classes.header}>
                        <h1 className={this.classes.title}>My Expertise</h1>
                        <p>I have experience across the stack!</p>
                    </div>
                    <Grid container spacing={24}>
                        <Grid item xs={12} sm={6}>
                            <div className={classNames({[this.classes.itemContainer]: true, [this.classes.frontendContainer]: true})}>
                                <img className={this.classes.image} src={FrontEndImage} alt="Front End" />
                                <div className={this.classes.typeDescription} >
                                    <h2>Front End</h2>
                                    <p className={this.classes.typeDescriptionText}>I love designing beautiful and elegant UI's with frameworks like React and Angular. I'm a minimalist who believes less is more.</p>
                                </div>
                            </div>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <div className={classNames({[this.classes.itemContainer]: true, [this.classes.backendContainer]: true})}>
                                <img className={this.classes.image} src={BackEndImage} alt="Back End"/>
                                <div className={this.classes.typeDescription} >
                                    <h2>Back End</h2>
                                    <p className={this.classes.typeDescriptionText}>An application with a robust and performant API is one that shines above all. I use Node and Express to architect powerful machinery.</p>
                                </div>
                            </div>
                        </Grid>
                    </Grid>
                    <Grid container spacing={24}>
                        <Grid item xs={12} sm={4}>
                            <div className={this.classes.itemContainer}>
                                <img className={this.classes.image} src={AngularImage} alt="Angular"/>
                            </div>
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <div className={this.classes.itemContainer}>
                                <img className={this.classes.image} src={ReactImage} alt="React"/>
                            </div>
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <div className={this.classes.itemContainer}>
                                <img className={this.classes.image} src={NodeImage} alt="Node"/>
                            </div>
                        </Grid>
                    </Grid>
                </div>
            </div>
        );
    }
}


About.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(About);



