import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';

const styles = theme => ({
    footerContainer: {
        backgroundColor: '#5c4f3c',
        color: 'white',
        padding: '2%',
        textAlign: 'center',
        letterSpacing: '1px',
        fontSize: '11px',
    },
    a: {
        color: 'white',
    },
});

class Hero extends Component {
    constructor(props) {
        super(props);
        this.classes = props.classes;
    }

    render() {
        return (
            <div className={this.classes.footerContainer}>
                <div>Icons made by <a className={this.classes.a} href="https://www.flaticon.com/authors/smashicons" title="Smashicons">Smashicons</a>, <a className={this.classes.a} href="https://www.flaticon.com/authors/swifticons" title="Swifticons">Swifticons</a>, <a className={this.classes.a} href="https://www.flaticon.com/authors/simpleicon" title="SimpleIcon">SimpleIcon</a>, <a className={this.classes.a} href="http://www.freepik.com" title="Freepik">Freepik</a>, <a className={this.classes.a} href="https://www.flaticon.com/authors/icomoon" title="Icomoon">Icomoon</a> from <a className={this.classes.a} href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a> is licensed by <a className={this.classes.a} href="http://creativecommons.org/licenses/by/3.0/" title="Creative Commons BY 3.0" target="_blank" rel="noopener noreferrer">CC 3.0 BY</a></div>
            </div>
        );
    }
}

Hero.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Hero);



